.card {
    margin-top: 50px;
    &_top {
        .row-flex();
    }
    &_figure {
        .col();
        .size(6);
        .size-sm(12);
        max-width: 719px;
        .md-block({
            max-width: inherit;
        });
    }
    &_img {
        height: 491px;
        background: #FFFFFF;
        box-shadow: 6px 6px 30px rgba(19, 17, 17, 0.09);
        display: flex;
        .sm-block({
            height: 320px;
        });
        img {
            margin: auto;
            .sm-block({
                height: 280px;
            });
        }
    }
    &_desc {
        .col();
        .size(3);
        .size-lg(4);
        .size-md(6);
        .size-sm(12);
        max-width: 324px; 
        margin-left: 56px;
        .lg-block({
            max-width: inherit;
        });
        .md-block({
            margin-left: 7.5px;
            max-width: 344px;
            padding-left: 20px;
        });
        .sm-block({
            max-width: inherit;
            padding-left: 0;
            margin-top: 40px;
        });
        &--title {
            .h1();
            font-size: 30px;
            line-height: 38px;
            letter-spacing: 0.02em;
            color: #083844;
            .xs-block({
                font-size: 26px;
                line-height: 34px;
            });
        }
        &--h3 {
            margin-top: 25px;
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            .o700();
            .xs-block({
                font-size: 18px;
                line-height: 26px;
                margin-top: 20px;
            });
        }
        &--li {
            margin-top: 12px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            p {
                font-size: 16px;
                color: #828282;
                line-height: 1.49;
                .o300();
            }
            p + p {
                color: #083844;
            }
        }
        &--btn {
            margin-top: 50px;
            .btn();
            width: 100%;
        }
    }
    &_bottom {
        margin-top: 50px;
        .row-flex();
        .sm-block({
            margin-top: 40px;
        });
    }
    &_p {
        .col();
        .size(6);
        .size-sm(12);
        max-width: 719px;
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            .o700();
            .xs-block({
                font-size: 18px;
                line-height: 26px;
            });
        }
        &--text {
            margin-top: 10px;
            font-size: 16px;
            line-height: 24px;
            color: #083844;
        }
        &--show {
            cursor: pointer;
            transition: .4s all ease;
            position: relative;
            top: 4px;
            &.active {
                img {
                    transform: rotate(270deg);
                }
            }
            img {
                transform: rotate(90deg);
            }
        }
    }
    &_advant {
        .col();
        .size(4);
        .size-sm(12);
        margin-left: 56px;
        .sm-block({
            margin: 40px 0 0;
        });
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            margin-left: 42px;
            .o700();
        }
        &--div {
            display: flex;
            align-items: center;
            margin-top: 12px;
        }
        &--icon {
            width: 32px;
            height: 32px;
            background: #F8F8F8;
            position: relative;
            border-radius: 100%;
            &::after {
                content: '';
                position: absolute;
                top: 35%; left: 50%;
                transform: translate(-50%);
                width: 10px;
                height: 10px;
                background: #C22328;
                border-radius: 100%;
            }
        }
        &--p {
            margin-left: 10px;
            font-size: 16px;
            color: #083844;
        }
    }
}

.also {
    margin-bottom: 50px;
    .xmd-block({
        margin-bottom: 20px;
    });
    .sm-block({
        margin-bottom: 40px;
    });
    &_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .xxs-block({
            flex-direction: column;
        });
    }
    &_title {
        font-size: 60px;
        line-height: 72px;
        letter-spacing: 0.02em;
        color: #083844;
        .o700();
        .md-block({
            font-size: 46px;
            line-height: 64px;
        });
        .xs-block({
            font-size: 26px;
            line-height: 34px;
        });
    }
    &_row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-top: 50px;
        .lg-block({
            flex-wrap: wrap;
        });
        .sm-block({
            display: none;
        });
    }
    &_arrow {
        width: 90px;
        height: 50px;
        background: #F8F8F8;
        position: relative;
        display: none;
        align-items: center;
        justify-content: space-around;
        .sm-block({
            display: flex;
        });
        .xxs-block({
            align-self: flex-end;
            margin-top: 12px;
        });
        &1,&2 {
            cursor: pointer;
            svg path {
                transition: .4s all ease;
            }
            &:hover {
                svg path {
                    fill: #BDBDBD;
                }
            }
        }
        &::after {
            content: '';
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%,-50%);
            background-color: #E0E0E0;
            width: 1px;
            height: 21px;
        }
    }
    &_slider {
        margin-top: 50px;
        display: none;
        .sm-block({
            display: block;
        });
    }
    &_item {
        display: block;
        width: 368px;
        .xlg-block({
            width: 330px;
        });
        .lg-block({
            width: 368px;
        }); 
        .xmd-block({
           width: 48%;
           margin-bottom: 30px;
        });
        .sm-block({
            width: inherit;
            max-width: 368px;
            margin: auto;
        });
        &:last-child {
            .lg-block({
                display: none;
            }); 
            .xmd-block({
                display: block;
            });
        }
        &--img {
            height: 241px;
            background: #FFFFFF;
            border: 1px solid #F2F2F2;
            box-shadow: 6px 6px 30px rgba(19, 17, 17, 0.09);
            display: flex;
            overflow: hidden;
            img {
                margin: auto;
            }
        }
        &--text {
            height: 110px;
            background: #083844;
            text-align: center;
            padding-top: 8px;
        }
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #FFFFFF;
            .o700();
        }
        &--desc {
            margin-top: 3px;
            font-size: 16px;
            line-height: 24px;
            color: #FFFFFF;
            .o300();
        }
    }
}