body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1550px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

html {
  font-size: 16px;
  line-height: 1.3;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

body {
  font-family: 'OpenSansRegular', 'Arial', sans-serif;
  height: 100%;
  position: relative;
  cursor: default;
}

ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.btn_mnu {
  width: 50px;
  height: 50px;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 4px 0;
  background-color: #000;
  position: relative;
  transition: all .5s;
}

.btn_mnu.active .btn_row:first-child {
  transform: rotate(-135deg);
  top: 10px;
}

.btn_mnu.active .btn_row:last-child {
  transform: rotate(-225deg);
  top: -10px;
}

.btn_mnu.active .btn_row:nth-child(2) {
  opacity: 0;
}

.h2 {
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.02em;
  font-family: 'Gotham Bold', 'Arial', sans-serif;
  position: relative;
  display: inline-block;
  color: #083844;
}

.h2::after {
  content: '';
  position: absolute;
  top: 55%;
  left: 107%;
  width: 173px;
  height: 3px;
  background-color: #C22328;
}

.section {
  margin-top: 110px;
}

.header {
  position: relative;
  z-index: 10;
  background-color: #fff;
}

.header_row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-between;
  align-items: center;
  height: 114px;
  padding-bottom: 9px;
}

.header_logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(16.666666666666668% - 20px);
  display: flex;
  align-items: center;
}

.header_logo--p {
  margin-left: 10px;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #36332D;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.header_nav {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(50% - 20px);
}

.header_nav.active {
  opacity: 1;
  z-index: 10;
}

.header_nav ul {
  display: flex;
  align-items: center;
}

.header_nav li:not(:first-child) {
  margin-left: 40px;
}

.header_nav li > a {
  color: #083844;
  font-size: 16px;
  line-height: 22px;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  position: relative;
  transition: .4s all ease;
}

.header_nav li > a:hover {
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.header_nav li > a::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 46px;
  height: 2px;
  background: #C22328;
  opacity: 0;
  transition: .4s all ease;
}

.header_nav li > a.active {
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.header_nav li > a.active::after {
  opacity: 1;
}

.header_tel {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(16.666666666666668% - 20px);
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  align-items: flex-end;
  padding-bottom: 5px;
}

.header_tel--a {
  display: flex;
}

.header_tel--a p {
  margin-left: 6px;
  font-size: 22px;
  line-height: 145%;
  letter-spacing: 0.02em;
  color: #083844;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.header_tel--btn {
  border-bottom: 1px solid #333333;
  font-size: 16px;
  line-height: 22px;
  color: #333333;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  margin-top: 4px;
}

.strip_row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  position: absolute;
  top: 0px;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  max-width: 1550px;
  margin: 0 auto;
  padding-left: 5px;
  padding-right: 5px;
}

.strip_col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  height: 100%;
  border-left: 1px solid #F2F2F2;
}

.strip_col.last {
  border-right: 1px solid #F2F2F2;
}

.intro {
  position: relative;
  background-position: bottom -75px right -119px;
  background-size: contain;
  background-repeat: no-repeat;
  min-height: 766px;
  max-width: 1920px;
  margin: auto;
}

.intro::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 55%;
  height: 89.5%;
  background: #F8F8F8;
  z-index: -1;
}

.intro_block {
  position: relative;
  padding-top: 115px;
}

.intro_block::before {
  content: 'skiff';
  position: absolute;
  left: 37%;
  bottom: -8%;
  font-size: 200px;
  line-height: 220px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: rgba(8, 56, 68, 0.04);
  font-family: 'Gotham Black', 'Arial', sans-serif;
}

.intro_h1 {
  font-size: 60px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #083844;
  max-width: 1180px;
  font-family: 'Gotham Black', 'Arial', sans-serif;
}

.intro_desc {
  position: relative;
  font-size: 22px;
  line-height: 30px;
  color: #083844;
  max-width: 690px;
  margin-top: 60px;
}

.intro_desc::after {
  content: '';
  position: absolute;
  top: -10px;
  left: 0;
  width: 173px;
  height: 3px;
  background-color: #C22328;
}

.intro_btn {
  margin-top: 70px;
  background-color: #C22328;
  padding: 16px 40px;
  display: inline-block;
  font-size: 22px;
  color: #fff;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  transition: .4s all ease;
}

.intro_btn:hover {
  background: linear-gradient(180deg, #BE0208 0%, #A00D11 100%);
}

.intro_down {
  position: absolute;
  bottom: 0;
  left: 46.3%;
  transform: translateX(-50%);
  width: 80px;
  height: 82px;
  background: #083844;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.why_row {
  height: 490px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 75px;
}

.why_item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 270px;
  position: relative;
}

.why_item::after {
  content: '';
  position: absolute;
  left: 0;
  top: -22px;
  width: 139px;
  height: 113px;
  z-index: -1;
  background: #F8F8F8;
}

.why_item:nth-child(odd) {
  align-self: flex-start;
}

.why_item--img {
  margin: auto;
}

.why_item--title {
  margin-top: 20px;
  font-size: 22px;
  line-height: 145%;
  letter-spacing: 0.02em;
  color: #083844;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.why_item--desc {
  margin-top: 12px;
  font-size: 16px;
  line-height: 150%;
  color: #083844;
  font-family: 'Open Sans Light', 'Arial', sans-serif;
}

.project_slider {
  margin-top: 50px;
}

.project_item {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.project_item--right {
  position: relative;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(58.333333333333336% - 20px);
}

.project_item--left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(41.66666666666667% - 20px);
}

.project_item--left img + img {
  margin-top: 17px;
}

.project_item--text {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 296px;
  height: 83px;
  background: #083844;
  padding-left: 25px;
  display: flex;
  align-items: center;
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.project_a {
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: #083844;
  border-bottom: 1px solid #333333;
  margin: 50px auto 0;
  display: block;
  text-align: center;
  width: 235px;
}

.banner {
  margin-top: 160px;
}

.banner_block {
  padding: 67px 53px 67px 45px;
  position: relative;
  background-color: #083844;
}

.banner_block::after {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  background: url(../img/banner.png) center / cover no-repeat;
  width: 669px;
  height: 482px;
}

.banner_block::before {
  content: 'baumit';
  position: absolute;
  top: 0;
  right: 15px;
  font-size: 200px;
  line-height: 220px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: rgba(224, 224, 224, 0.15);
  font-family: 'Gotham Black', 'Arial', sans-serif;
}

.banner_text--title {
  font-size: 30px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
  max-width: 625px;
}

.banner_text--desc {
  margin-top: 30px;
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  position: relative;
  max-width: 550px;
}

.banner_text--desc::after {
  content: '';
  position: absolute;
  top: -8px;
  left: 0;
  width: 173px;
  height: 3px;
  background-color: #C22328;
}

.banner_text--btn {
  background-color: #C22328;
  padding: 16px 40px;
  display: inline-block;
  font-size: 22px;
  color: #fff;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  transition: .4s all ease;
  margin-top: 24px;
}

.banner_text--btn:hover {
  background: linear-gradient(180deg, #BE0208 0%, #A00D11 100%);
}

.about_row {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.about_text {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(50% - 20px);
}

.about_p {
  margin-bottom: 25px;
  font-size: 16px;
  line-height: 150%;
  color: #083844;
  font-family: 'Open Sans Light', 'Arial', sans-serif;
}

.about_img {
  position: relative;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(50% - 20px);
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.about_img::before {
  content: '';
  position: absolute;
  top: -50px;
  left: 50px;
  width: 524px;
  height: 529px;
  background: #F8F8F8;
  z-index: -1;
}

.about_img--div {
  position: relative;
}

.about_img--div::after {
  content: '';
  position: absolute;
  bottom: 4px;
  right: 0;
  width: 192px;
  height: 82px;
  background: #083844;
}

.about_img img {
  width: 612px;
  height: 476px;
  object-fit: cover;
}

.about_more {
  display: none;
  cursor: pointer;
  margin-top: 20px;
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: #083844;
  border-bottom: 1px solid #083844;
}

.form {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 77px 0;
}

.form_h2 {
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  text-align: center;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.form_desc {
  margin-top: 20px;
  font-size: 22px;
  line-height: 32px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  font-family: 'OpenSansBold', 'Arial', sans-serif;
}

.form_row {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form_label {
  display: flex;
  flex-direction: column;
}

.form_label span {
  font-size: 16px;
  font-family: 'Open Sans Light', 'Arial', sans-serif;
  color: #fff;
  margin-left: 8px;
}

.form_label input {
  width: 320px;
  height: 62px;
  background: #FFFFFF;
  padding: 0 20px;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 0.02em;
  color: #083844;
  margin: 3px 10px 0;
}

.form_btn {
  background-color: #C22328;
  padding: 16px 40px;
  display: inline-block;
  font-size: 22px;
  color: #fff;
  font-family: 'OpenSansSemiBold', 'Arial', sans-serif;
  transition: .4s all ease;
  margin: 22px 10px 0;
}

.form_btn:hover {
  background: linear-gradient(180deg, #BE0208 0%, #A00D11 100%);
}

.spons {
  position: relative;
  z-index: 10;
  background-color: #fff;
  padding: 110px 0;
}

.spons_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.spons_arrow {
  width: 139px;
  height: 82px;
  background: #F8F8F8;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.spons_arrow1,
.spons_arrow2 {
  cursor: pointer;
}

.spons_arrow1 svg path,
.spons_arrow2 svg path {
  transition: .4s all ease;
}

.spons_arrow1:hover svg path,
.spons_arrow2:hover svg path {
  fill: #BDBDBD;
}

.spons_arrow::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #E0E0E0;
  width: 1px;
  height: 21px;
}

.spons_slider {
  margin-top: 50px;
}

.contacts {
  position: relative;
  min-height: 621px;
}

.contacts_map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 621px;
  z-index: -1;
  filter: grayscale(100%);
}

.contacts_row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: flex-end;
  align-items: flex-start;
  padding-top: 100px;
}

.contacts_blue {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(41.66666666666667% - 20px);
  margin-right: calc(8.333333333333334% + 10px);
  background: #083844;
  padding: 50px 0 63px 0;
  color: #fff;
}

.contacts_blue--title {
  text-align: center;
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  font-family: 'Gotham Bold', 'Arial', sans-serif;
}

.contacts_blue--ul {
  margin-top: 35px;
  padding-left: 75px;
}

.contacts_blue--li {
  margin-bottom: 20px;
  display: flex;
  align-items: flex-start;
}

.contacts_blue--li p {
  margin-left: 14px;
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  font-family: 'Gotham Bold', 'Arial', sans-serif;
  max-width: 385px;
}

.contacts_blue--li img {
  position: relative;
  top: 4px;
}

.footer {
  position: relative;
  z-index: 10;
  background-color: #fff;
}

.footer_inner {
  background-color: #F8F8F8;
}

.footer_row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-between;
  align-items: center;
  height: inherit;
  padding: 30px 0;
}

.footer_left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(16.666666666666668% - 20px);
}

.footer_site {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.footer_site:hover .footer_site--img {
  opacity: 1;
}

.footer_site:hover .footer_site--p {
  color: #000;
}

.footer_site:hover {
  text-decoration: none;
}

.footer_site--img {
  width: 17px;
  height: 17px;
  opacity: .2;
  transition: .4s all;
}

