.build {
    position: relative;
    min-height: 769px;
    .md-block({
        margin-top: 67px;
        min-height: 530px;
    });
    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        width: 71%;
        height: 87%;
        background: #F8F8F8;
        z-index: -1;
        .md-block({
            width: 72%;
            height: 448px;
        });
        .sm-block({
            height: 229px;
            width: 100%;
        });
    } 
    &_counter {
        position: absolute;
        bottom: 0;
        left: 80px;
        z-index: 1;
        font-size: 56px;
        line-height: 67px;
        letter-spacing: 0.02em;
        color: #083844;
        pointer-events: none;
        .o700();
        width: 150px;
        height: 82px;
        background: #F8F8F8;
        padding-left: 40px;
        padding-top: 8px;
        .sm-block({
            bottom: 135px;
        });
        .xs-block({
            left: auto;
            right: 221px;
        });
        .xxs-block({
            left: auto;
            right: 181px;
        });
    }
    &_arrow {
        width: 110px;
        height: 82px;
        background: #F8F8F8;
        display: flex;
        align-items: center;
        justify-content: space-around;
        position: absolute;
        bottom: 0;
        left: 229px;
        .sm-block({
            bottom: 135px;
        });
        .xs-block({
            right: 113px;
            left: auto;
        });
        .xxs-block({
            right: 72px;
            left: auto;
        });
        &1,&2 {
            cursor: pointer;
            svg path {
                transition: .4s all ease;
            }
            &:hover {
                svg path {
                    fill: #BDBDBD;
                }
            }
        }
        &::after {
            content: '';
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%,-50%);
            background-color: #E0E0E0;
            width: 1px;
            height: 21px;
        }
    }
    &_down {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 80px;
        height: 82px;
        background: #083844;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        .sm-block({
            display: none;
        });
        img {
            .md-block({
                width: 30px;
                height: 30px;
            });
        }
    }
    &_img {
        position: absolute;
        top: 131px;
        right: -199px;
        background-size: cover;
        background-repeat: no-repeat;
        width: 877px;
        height: 638px;
        .xlg-block({
            right: -420px;
        });
        .xmd-block({
            height: 530px;
            top: 67px;
        });
        .md-block({
            width: 396px;
            height: 435px;
            right: -15px;
            top: 94px;
        });
        .sm-block({
            top: 230px;
            left: -15px;
            right: -15px;
            width: calc(100% + 30px);
        });  
    }
    &_images {
        display: flex;
        margin: 75px -12px 0;
        .xmd-block({
            display: none;
        });
        &--item {
            margin: 0 12px;
            width: 250px;
            height: 238px;
            img {
                object-fit: cover;
            }
        }
    }
    &_block {
        position: relative;
        padding-top: 115px; 
        .xmd-block({
            padding-top: 50px;
        });
        .md-block({
            padding-top: 12px;
        });
        .sm-block({
            padding-top: 22px;
        });
    }
    &_h1 {
        font-size: 60px;
        line-height: 120%;
        letter-spacing: 0.02em;
        color: #083844;
        max-width: 1180px;
        .h1();
        .md-block({
            font-size: 40px;
            line-height: 56px;
            max-width: 725px;
        });
        .sm-block({
            font-size: 26px;
            line-height: 32px;
            max-width: 550px;
        });
        .xs-block({
            font-size: 18px;
            line-height: 26px;
            letter-spacing: 0.04em;
        });
    }
    &_desc {
        position: relative;
        font-size: 22px;
        line-height: 30px;
        color: #083844;
        max-width: 690px;
        margin-top: 50px;
        .xmd-block({
            max-width: 444px;
        });
        .md-block({
            max-width: 333px;
        });
        .sm-block({
            font-size: 18px;
            line-height: 24px;
            max-width: inherit;
        });
        .xs-block({
            font-size: 14px;
            line-height: 20px;
        });
        &::after {
            content: '';
            position: absolute;
            top: -30px; left: 0;
            width: 173px;
            height: 3px;
            background-color: @red;
            .md-block({
                top: -30px;
            });
            .xs-block({
                top: -25px;
            });
        }
    }
    &_btn {
        margin-top: 70px;
        .btn();
        .xs-block({
            margin-top: 25px;
        });
    }
}

@media screen and (max-width:1560px) {
    .build_img {
        right: -270px;
    }
}


.our {
    &_row {
        .row-flex();
        margin-top: 50px;
        .xs-block({
            margin-top: 20px;
        });
    }
    &_h2 {
        max-width: 1120px;
        .lg-block({
            max-width: 1000px;
        });
        .xmd-block({
            max-width: 800px;
        });
        &::after {
            top: 26%;
            left: 102%;
            .lg-block({
                left: 92%;
            });
            .xmd-block({
                top: 86%;
                left: 22%;
            });
            .md-block({
                left: 22%;
            });
            .sm-block({
                top: 113%;
            });
        }
    }
    &_img {
        position: relative;
        margin-right: 96px;
        .md-block({
            margin-right: 65px;
        });
        .sm-block({
            display: none;
        });
        &::after {
            content: '';
            position: absolute;
            top: 23px; right: -75px;
            background: #F8F8F8;
            width: 368px;
            height: 317px;
            z-index: -1;
            .md-block({
                width: 329px;
                height: 300px;
                right: -36px;
            });
        }
        img {
            object-fit: cover;
            .md-block({
                width: 309px;
                height: 296px;
            });
        }
    }
    &_item {
        .col();
        .size(3.5);
        .size-lg(6);
        .size-xmd(5);
        .size-md(6);
        .size-sm(10);
        .md-block({
            margin-top: 44px;
        });
        .xs-block({
            margin-top: 20px;
        });
        &:nth-child(n+3) {
            .lg-block({
                margin-top: 70px;
            });
            .md-block({
                margin-top: 44px;
            });
            .xs-block({
                margin-top: 20px;
            });
        }
        &:nth-child(n+4) {
            margin-top: 70px;
            .md-block({
                margin-top: 44px;
            });
            .xs-block({
                margin-top: 20px;
            });
        }
        &:nth-child(4) {
            .xs-block({
                margin-top: 20px;
            });
        }
        &:nth-child(5) {
            margin-left: 80px;
            .lg-block({
                margin-left: 10px;
            });
        }
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            .o700();
            position: relative;
            .sm-block({
                font-size: 18px;
                line-height: 26px;
            });
            &::after {
                content: '';
                position: absolute;
                width: 173px;
                height: 3px;
                background: #C22328;
                bottom: -13px;left: 0;
            }
        }
        &--desc {
            margin-top: 27px;
            font-size: 16px;
            line-height: 24px;
            color: #083844;
            .o300();
            .lg-block({
                max-width: 460px;
            });
        }
    }
}

.work {
    &_row {
        margin-top: 50px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        .xmd-block({
            justify-content: flex-start;
        });
    }
    &_img {
        position: relative;
        .sm-block({
            display: none;
        });
        .xlg-block({
            width: 575px;
        });
        .lg-block({
            width: 480px;
        });
        .xmd-block({
            width: 261px;
            height: 388px;
        });
        &::after {
            content: '';
            position: absolute;
            bottom: -62px; right: -42px;
            background: #F8F8F8;
            width: 487px;
            height: 497px;
            z-index: -1;
            .lg-block({
                width: 329px;
                height: 300px;
                bottom: -30px; 
                right: -30px;
            });
            .xmd-block({
                width: 281px;
                height: 356px;
                bottom: -42px;
                right: -22px;
            });
        }
        img {
            object-fit: cover;
            .xmd-block({
                width: 261px;
                height: 388px;
            });
        }
    }
    &_text {
        .xmd-block({
            margin-left: 42px;
        });
        .sm-block({
            margin-left: 0;
            width: 100%;
        });
    }
    &_div {
        display: flex;
        align-items: flex-start;
        margin-bottom: 30px;
        .md-block({
            margin-bottom: 20px;
        });
        &--text {
            margin-left: 10px;
            max-width: 670px;
            .sm-block({
                max-width: inherit;
            });
        }
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            .o700();
            .sm-block({
                font-size: 18px;
                line-height: 26px;
            });
        }
        &--icon {
            width: 32px;
            height: 32px;
            background: #F8F8F8;
            position: relative;
            border-radius: 100%;
            .sm-block({
                display: inline-table;
            });
            &::after {
                content: '';
                position: absolute;
                top: 35%; left: 50%;
                transform: translate(-50%);
                width: 10px;
                height: 10px;
                background: #C22328;
                border-radius: 100%;
            }
        }
        &--p {
            margin-top: 12px;
            font-size: 16px;
            color: #083844;
            max-width: 620px;
            .sm-block({
                max-width: 290px;
            });
        }
    }
}

