.catalog {
    margin: 50px 0 65px;
    position: relative;
    z-index: 11;
    &_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &_h2 {
        .lg-block({
            display: none;
        });
    }
    &_search {
        display: flex;
        align-items: center;
        position: relative;
        input {
            width: 303px;
            height: 56px;
            background: #F8F8F8;
            border: 1px solid #F2F2F2;
            padding: 0 20px;
            .xs-block({
                position: absolute;
                top: 0; right: 58px;
                width: 200px;
                display: none;
            });
        }
        button {
            width: 56px;
            height: 56px;
            background: #C22328;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    &_row {
        .row-flex();
        align-items: flex-start;
        margin-top: 50px;
    }
    &_btn {
        .btn();
        display: none;
        .lg-block({
            display: inline-block;
        });
    }
    &_filter {
        .col();
        .size(3);
        background: #F8F8F8;
        padding: 26px 0 230px 30px;
        .lg-block({
            display: none;
        });
        &--title {
            font-size: 30px;
            line-height: 38px;
            letter-spacing: 0.02em;
            color: #083844;
            .h1();
        }
        &--h3 {
            margin-top: 30px;
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #083844;
            .o700();
        }
        &--h4 {
            margin-top: 20px;
            font-size: 16px;
            line-height: 22px;
            color: #083844;
            .o600();
        }
        &--p {
            margin-top: 10px;
            font-size: 16px;
            line-height: 24px;
            color: #083844;
            padding-left: 20px;
        }
    }
    &_check {
        display: flex;
        align-items: center;
        cursor: pointer;
        &--first {
            margin-top: 24px;
        }
        &--item {
            input {
                display: none;
            }
            i {
                width: 18px;
                height: 18px;
                border: 1px solid #083844;
                position: relative;
                display: inline-block;
                &::after {
                    content: '';
                    position: absolute;
                    top: 5px; left: 3px;
                    background: url(../img/check.svg);
                    width: 11px;
                    height: 7px;
                    opacity: 0;
                    transition: .4s all ease;
                }
            }
            input:checked+i::after {
                opacity: 1;
            }
        }
        p {
            margin-left: 8px;
            font-size: 16px;
            line-height: 22px;
            color: #083844;
            .o600();
        }
    }
    &_right {
        .col();
        .size(9);
        .size-lg(12);
    }
    &_flex {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
        .lg-block({
            margin-top: 10px;
        });
    }
    &_item {
        width: 368px;
        margin-bottom: 30px;
        display: block;
        .xlg-block({
            width: 330px;
        });
        .lg-block({
            width: 368px;
        });
        .md-block({
            width: 48%;
        });
        .xs-block({
            width: 100%;
        });
        &--img {
            height: 241px;
            background: #FFFFFF;
            border: 1px solid #F2F2F2;
            box-shadow: 6px 6px 30px rgba(19, 17, 17, 0.09);
            display: flex;
            overflow: hidden;
            img {
                margin: auto;
            }
        }
        &--text {
            height: 110px;
            background: #083844;
            text-align: center;
            padding-top: 8px;
        }
        &--title {
            font-size: 22px;
            line-height: 32px;
            letter-spacing: 0.02em;
            color: #FFFFFF;
            .o700();
        }
        &--desc {
            margin-top: 3px;
            font-size: 16px;
            line-height: 24px;
            color: #FFFFFF;
            padding: 0 10px;
            .o300();
        }
    }
    &_more {
        width: 368px;
        height: 351px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #F8F8F8;
        cursor: pointer;
        &--img {
            margin-top: 99px;
        }
        &--p {
            margin-top: 32px;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            color: #4F4F4F;
            .o300();
        }
    }
}

.pagination {
    margin: 50px auto 0;
    .md-block({
        display: none;
    });
    &_list {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &_item {
        margin: 0 10px;
    }
    &_link {
        display: inline-block;
        width: 32px;
        height: 32px;
        font-size: 16px;
        line-height: 32px;
        background: #F8F8F8;
        color: #083844;
        text-align: center;
        transition: .4s all ease;
        &:hover {
            background: #083844;
            color: #fff;
        }
        .o600();
        &__active {
            background: #083844;
            color: #fff;
        }
    }
    &_dots {
        font-size: 16px;
        color: #083844;
        text-align: center;
        .o600();
    }
}

.filter {
    position: absolute;
    top: 0; left: 0;
    width: 373px;
    padding: 26px 0 200px 30px;
    cursor: default !important;
    .fancybox-close-small {
        padding: 0;
        color: #1D1D24;
        top: 5px; right: 5px;
    }
}